import { sanitize } from 'dompurify';

/**
 * Loop over DOM nodes
 * @param  {array}   array
 * @param  {function} callback
 * @param  {scope}   scope
 * @return {void}
 */
export function forEach(array, callback, scope) {
  for (let i = 0, { length } = array; i < length; i += 1) {
    callback.call(scope, i, array[i]);
  }
}

/**
 * Pull item out of array
 * @param  {array} array
 * @param  {string} key
 * @return {array}
 */
export function pluck(array, key) {
  return array.map(o => o[key]);
}

/**
 * Used for more performant scroll listeners
 * Example: window.addEventListener('scroll', throttleScroll(() => console.log('PERFORMANCE!')));
 *
 * @param  {function} action
 * @return {function}
 */
export function throttleScroll(action) {
  let isRunning = false;
  // eslint-disable-next-line func-names
  return function () {
    if (isRunning) return;
    isRunning = true;
    window.requestAnimationFrame(() => {
      action();
      isRunning = false;
    });
  };
}

/**
 * Returns node representation of string (or just the node if already provided)
 * @param  {string|node} el Either a string representation of a node selector OR a node
 * @return {mixed} Will return `false` on a non-existent node or the node
 */
export function stringOrNode(el) {
  let $element = el;

  // If no value is given or selector is not available
  if (!$element) {
    return false;
  }

  // If value is not a node
  if (!$element.nodeType) {
    $element = document.querySelector(el);
  }

  return $element;
}

/**
 * Grab element options
 * @param  {node} $el
 * @return {object}
 */
export function getOptions($el) {
  const options = $el.getAttribute('data-options');
  let optionsObject = {};

  if (options) {
    optionsObject = JSON.parse(sanitize(options));
  }

  return optionsObject;
}

/**
 * Taken from https://www.npmjs.com/package/uuid
 *
 * @param  {string} a
 * @return {string}
 */
export function generateUUID(a) {
  return a ? (a ^ Math.random() * 16 >> a / 4).toString(16) : ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, generateUUID); // eslint-disable-line
}

/**
 * Return value from CSS var
 *
 * @param  {string} variable
 * @return {mixed}
 */
export function getVar(variable) {
  return getComputedStyle(document.body).getPropertyValue(variable);
}
